// AdminTools.js
import React from 'react';

const AdminTools = () => (
  <div className="bg-white p-4 rounded-lg shadow mb-6">
    <h2 className="text-xl font-bold mb-4">Administrative Tools</h2>
    <div className="grid grid-cols-2 gap-4">
      <button className="bg-blue-500 text-white p-2 rounded">Add/Edit Hospital Data</button>
      <button className="bg-green-500 text-white p-2 rounded">Upload Data</button>
      <button className="bg-purple-500 text-white p-2 rounded">Generate Reports</button>
      <button className="bg-orange-500 text-white p-2 rounded">Live Data Integration</button>
    </div>
  </div>
);

export default AdminTools;
